import { createClient } from '@supabase/supabase-js';
import { config } from '../config/test';
import { Database } from './database.types';

const isTest = process.env.NODE_ENV === 'test';

const supabaseUrl = isTest
  ? 'https://mock-supabase-url.com'
  : import.meta.env.VITE_SUPABASE_URL;

const supabaseAnonKey = isTest
  ? 'mock-anon-key'
  : import.meta.env.VITE_SUPABASE_ANON_KEY;

// Verificao mais detalhada das variveis de ambiente
if (!isTest) {
  if (!supabaseUrl) {
    console.error('VITE_SUPABASE_URL no est definida no arquivo .env');
    throw new Error('VITE_SUPABASE_URL no est definida. Verifique o arquivo .env');
  }

  if (!supabaseAnonKey) {
    console.error('VITE_SUPABASE_ANON_KEY no est definida no arquivo .env');
    throw new Error('VITE_SUPABASE_ANON_KEY no est definida. Verifique o arquivo .env');
  }

  // Validao bsica do formato da URL
  try {
    new URL(supabaseUrl);
  } catch (error) {
    console.error('VITE_SUPABASE_URL no  uma URL vlida:', supabaseUrl);
    throw new Error('VITE_SUPABASE_URL no  uma URL vlida');
  }

  // Validao bsica da chave annima
  if (supabaseAnonKey.length < 10) {
    console.error('VITE_SUPABASE_ANON_KEY parece estar em formato invlido');
    throw new Error('VITE_SUPABASE_ANON_KEY parece estar em formato invlido');
  }
}

console.log('Supabase URL:', supabaseUrl);
console.log('Supabase Anon Key:', supabaseAnonKey ? 'Presente' : 'Ausente');

export const supabase = createClient<Database>(supabaseUrl, supabaseAnonKey, {
  auth: {
    persistSession: true,
    autoRefreshToken: true,
    detectSessionInUrl: true
  }
});

export async function setupIntegrationPolicies() {
  console.log('Iniciando setupIntegrationPolicies');
  try {
    const { data: { session }, error: sessionError } = await supabase.auth.getSession();
    
    if (sessionError) {
      console.error('Erro ao buscar sesso:', sessionError);
      throw sessionError;
    }
    
    if (!session?.user) {
      console.log('Usurio no autenticado');
      throw new Error('Usurio no autenticado');
    }

    console.log('Verificando integraes existentes para usurio:', session.user.id);
    
    // Verificar se j existem registros para o usurio
    const { data: existingIntegrations, error: fetchError } = await supabase
      .from('user_integrations')
      .select('id')
      .eq('user_id', session.user.id)
      .limit(1);

    if (fetchError) {
      console.error('Erro ao buscar integraes existentes:', fetchError);
      throw fetchError;
    }

    // Se no houver registros, criar os registros iniciais
    if (!existingIntegrations || existingIntegrations.length === 0) {
      console.log('Criando registros iniciais de integrao');
      const { error: insertError } = await supabase
        .from('user_integrations')
        .insert([
          {
            user_id: session.user.id,
            provider: 'google',
            access_token: '', // Token vazio inicial
            refresh_token: null, // Token de atualizao nulo inicial
            expires_at: null, // Data de expirao nula inicial
            scope: null // Escopo nulo inicial
          },
          {
            user_id: session.user.id,
            provider: 'github',
            access_token: '', // Token vazio inicial
            refresh_token: null, // Token de atualizao nulo inicial
            expires_at: null, // Data de expirao nula inicial
            scope: null // Escopo nulo inicial
          }
        ]);

      if (insertError) {
        console.error('Erro ao criar registros iniciais:', insertError);
        throw insertError;
      }
      
      console.log('Registros iniciais criados com sucesso');
    } else {
      console.log('Integraes j existem para o usurio');
    }
  } catch (error) {
    console.error('Erro ao configurar polticas de integrao:', error);
    throw error;
  }
} 